html {
  box-sizing: border-box; }

body {
  font-family: "Montserrat", "Arial", sans-serif; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/montserrat-v15-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/montserrat-v15-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-500.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/montserrat-v15-latin_cyrillic-600.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-600.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-v15-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/montserrat-v15-latin_cyrillic-700.woff") format("woff"); }

.page__header-wrap {
  max-width: 1366px;
  height: 650px;
  margin-left: auto;
  margin-right: auto;
  background-color: #3a679a;
  background-image: url("../img/bg_intro_desktop_1x.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page__header-wrap {
      background-image: url("../img/bg_intro_desktop_2x.jpg"); } }

@media (max-width: 1023px) {
  .page__header-wrap {
    max-width: 768px;
    height: 638px;
    background-image: url("../img/bg_intro_tablet_1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .page__header-wrap {
      background-image: url("../img/bg_intro_tablet_2x.jpg"); } }

@media (max-width: 767px) {
  .page__header-wrap {
    max-width: 320px;
    height: 500px;
    background-image: url("../img/bg_intro_mobile_1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page__header-wrap {
      background-image: url("../img/bg_intro_mobile_2x.jpg"); } }

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  padding-top: 35px;
  padding-right: 103px;
  padding-left: 103px; }

.page-header__logo {
  width: 70px;
  height: 38px; }

.page-header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .page-header__container {
    padding-top: 30px;
    padding-right: 45px;
    padding-left: 45px; }
  .page-header__logo {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .page-header__container {
    flex-wrap: wrap;
    padding-top: 20px;
    padding-right: 15px;
    padding-left: 15px; }
  .page-header__logo {
    width: 42px;
    height: 22px;
    margin-right: 40px; }
  .page-header--opened,
  .page-header--nojs {
    background-color: #f9fbfd; }
  .page-header--closed .page-header__toggle {
    display: block;
    width: 24px;
    height: 12px;
    background-color: transparent;
    background-image: url("../img/icon-burger.svg");
    background-size: 20px auto;
    border: 0;
    cursor: pointer; }
  .page-header--opened .page-header__toggle {
    display: block;
    width: 13px;
    height: 13px;
    background-color: transparent;
    background-image: url("../img/icon-cross.svg");
    background-size: 13px auto;
    border: 0;
    cursor: pointer; }
  .page-header--nojs .page-header__toggle {
    display: none; } }

.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.nav__list-item:not(:last-child) {
  margin-right: 33px; }

.nav__list-item a {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd; }

.nav__toggle {
  display: none; }

@media (max-width: 1023px) {
  .nav__list-item:not(:last-child) {
    margin-right: 23px; }
  .nav__list-item a {
    font-size: 14px;
    line-height: 17px;
    color: #f9fbfd; } }

@media (max-width: 767px) {
  .page-header--closed .nav {
    display: none; }
  .page-header--nojs .nav {
    display: block; }
  .page-header--opened .nav__list,
  .page-header--nojs .nav__list {
    flex-direction: column; }
  .page-header--opened .nav__list-item,
  .page-header--nojs .nav__list-item {
    padding-bottom: 30px; }
    .page-header--opened .nav__list-item:first-child,
    .page-header--nojs .nav__list-item:first-child {
      padding-top: 50px; }
    .page-header--opened .nav__list-item:last-child,
    .page-header--nojs .nav__list-item:last-child {
      padding-bottom: 65px; }
    .page-header--opened .nav__list-item a,
    .page-header--nojs .nav__list-item a {
      font-size: 18px;
      line-height: 22px;
      color: #011c40; } }
